@mixin base() {
    background-color: #3a80aa;
    border-radius: 1rem;
    color: #fff;
    margin: 0 auto;
    padding: 2rem;
    text-align: center;
    text-decoration: none;
    width: 25rem;

    @media all and (min-width: 768px) {
        margin: initial;
        width: 40rem;
    }
}

&__button {
    @include base;
}